	border: 1px solid rgba( var( --color-border ), 0.5 );
	box-shadow: 0 1px 2px 0px rgba(0, 0, 0, 0.05);
}
a img:hover, a img:focus {
	border-color: rgba( var( --color-primary-link ), 0.25 );
}

audio, video, object, embed, iframe {
	width: 100%;
}

blockquote {
	margin: 0;
	padding: 0 2.125rem;
	font-style: italic;
	color: rgb( var( --color-theme-primary ) );
}
blockquote i,
blockquote em,
blockquote cite {
